@import "../main.scss";

::-webkit-scrollbar {
  width: 0;
}

.emotionsMap {
  display: inline-block;
  height: 85%;
  overflow: hidden;
}

.label {
  fill: $lightColor;
  font-family: "Rubik Regular";
  font-size: 24pt;
}

.regularDot {
  cursor: pointer;
  transition: fill 0.3s linear, opacity 0.5s linear;
}

.nonActiveDot {
  fill: $darkColor;
}

.lowOpacity {
  opacity: 0.6;
}

.emotionsContent {
  display: inline-block;
  position: absolute;
  top: 3%;
  right: 15%;
}

.diamond {
  fill: none;
  stroke: $lightColor;
  stroke-miterlimit: 10;
}

.infoBoxEmotion {
  top: 20%;
  left: 20%;
}

.infoBoxSearch {
  top: 20%;
  right: 30px;
}

@keyframes breathe {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
